#acfw_add_products {
  padding-bottom: 51px;
}
#acfw_add_products .add-before-cart-condition-check-field {
  display: inline-block;
  margin-left: 10px;
}
#acfw_add_products .add-before-cart-condition-check-field label {
  float: none;
  margin: 0;
  width: auto;
  font-size: 12px;
}
#acfw_add_products .add-before-cart-condition-check-field label input {
  margin-top: -2px;
}
#acfw_add_products .add-before-cart-condition-check-field label span {
  display: inline-block;
  margin-left: 5px;
}

.woocommerce_options_panel .date-time-field {
  float: left;
  display: flex;
  width: 50%;
  align-items: top;
  height: 30px;
}
.woocommerce_options_panel .date-time-field input[type=text].date-field {
  width: calc(100% - 135px);
  margin-right: 5px;
}
.woocommerce_options_panel .date-time-field span {
  margin-right: 5px;
}
.woocommerce_options_panel .date-time-field input.date-hour {
  width: 60px;
  margin-right: 5px;
}
.woocommerce_options_panel .date-time-field input.date-minute {
  width: 60px;
}
.woocommerce_options_panel .clear-scheduler-fields {
  position: relative;
  top: 2px;
  display: inline-block;
  color: #dc3232;
  text-decoration: none;
  box-shadow: none !important;
}
.woocommerce_options_panel .acfw-sort-coupon-priority-field select {
  width: 25%;
  margin-right: 5px;
}
.woocommerce_options_panel .acfw-sort-coupon-priority-field input[type=number] {
  width: calc(25% - 10px);
}

#acfw_bogo_deals .bogo-auto-add-products-field {
  display: none;
}
#acfw_bogo_deals .bogo-auto-add-products-field.show {
  display: block;
}